(function() {
  (function(ng, JSON) {
    var MainController, ReaderDialogController, SettingsDialogController;
    SettingsDialogController = [
      '$scope', '$mdDialog', 'settings', function($scope, $mdDialog, settings) {
        ng.extend($scope, settings);
        return $scope.handleAction = function(action) {
          return $mdDialog.hide($scope);
        };
      }
    ];
    ReaderDialogController = [
      '$scope', '$mdDialog', 'ScripturePassage', 'verses', function($scope, $mdDialog, ScripturePassage, verses) {
        $scope.cancel = function() {
          return $mdDialog.hide($scope);
        };
        $scope.readers = ScripturePassage.readers;
        return $scope.openReader = function(reader) {
          return reader.load(verses);
        };
      }
    ];
    MainController = function(moment, ReadingPlanData, LocalStorage, ScripturePassage, $mdDialog, $timeout) {
      var LOCAL_STORAGE_KEY, load, moveCurrentDate, selectedPlanTimeframe, selectedPlanType, settings, updateTodaysReading, vm;
      LOCAL_STORAGE_KEY = 'readdaily.settings';
      vm = this;
      vm.version = '1.3.0';
      vm.currentDate = moment();
      updateTodaysReading = function() {
        var format, today;
        format = selectedPlanTimeframe();
        today = vm.currentDate;
        vm.dateLabel = today.format('ddd, MMMM Do');
        return ReadingPlanData.dayPlan(vm.selectedPlanType, today).then(function(fullPlan) {
          var reading;
          reading = [];
          switch (format) {
            case '1':
              if (fullPlan.otReading) {
                reading.push({
                  name: 'Old Testament 1',
                  verses: fullPlan.otReading
                });
              }
              if (fullPlan.ot2Reading) {
                reading.push({
                  name: 'Old Testament 2',
                  verses: fullPlan.ot2Reading
                });
              }
              break;
            case '2:1':
              if (fullPlan.otReading) {
                reading.push({
                  name: 'Old Testament',
                  verses: fullPlan.otReading
                });
              }
              break;
            case '2:2':
              if (fullPlan.ot2Reading) {
                reading.push({
                  name: 'Old Testament',
                  verses: fullPlan.ot2Reading
                });
              }
          }
          if (fullPlan.psalmsReading) {
            reading.push({
              name: 'Psalms',
              verses: fullPlan.psalmsReading
            });
          }
          if (fullPlan.gapReading) {
            reading.push({
              name: 'Gospels/Acts/Proverbs',
              verses: fullPlan.gapReading
            });
          }
          if (fullPlan.letterReading) {
            reading.push({
              name: 'Letters',
              verses: fullPlan.letterReading
            });
          }
          vm.reading = reading;
          return vm.loaded = true;
        });
      };
      load = function() {
        vm.selectedPlanType = selectedPlanType();
        vm.selectedPlanTimeframe = selectedPlanTimeframe();
        vm.selectedPlanTypeLabel = vm.planTypeOptions[vm.selectedPlanType];
        vm.selectedPlanTimeframeLabel = vm.planTimeframeOptions[vm.selectedPlanTimeframe];
        vm.loaded = false;
        return updateTodaysReading();
      };
      vm.showAbout = function(ev) {
        var updateSettings;
        updateSettings = function(config) {
          console.log(config.selectedPlanType, config.selectedPlanTimeframe);
          settings('selectedPlanType', config.selectedPlanType);
          settings('selectedPlanTimeframe', config.selectedPlanTimeframe);
          return load();
        };
        return $mdDialog.show({
          controller: SettingsDialogController,
          templateUrl: 'app/main/settings-dialog-template.html',
          locals: {
            settings: vm
          },
          parent: angular.element(document.body),
          targetEvent: ev,
          clickOutsideToClose: true
        }).then(updateSettings);
      };
      vm.showReaderChoice = function(verses, ev) {
        if (ScripturePassage.readers.length === 1) {
          return ScripturePassage.readers[0].load(verses);
        } else {
          return $mdDialog.show({
            controller: ReaderDialogController,
            templateUrl: 'app/main/readers-dialog-template.html',
            locals: {
              verses: verses
            },
            parent: angular.element(document.body),
            targetEvent: ev,
            clickOutsideToClose: true
          });
        }
      };
      settings = function(key, value) {
        var _settings;
        _settings = JSON.parse(LocalStorage.get(LOCAL_STORAGE_KEY)) || {};
        if (value != null) {
          _settings[key] = value;
          LocalStorage.set(LOCAL_STORAGE_KEY, JSON.stringify(_settings));
        } else {
          return _settings[key];
        }
      };
      vm.planTimeframeOptions = {
        '1': 'All In 1 Year',
        '2:1': '2yr Plan - Year 1',
        '2:2': '2yr Plan - Year 2'
      };
      vm.planTypeOptions = {
        'chapter': 'Chapter Based'
      };
      selectedPlanType = function() {
        return settings('selectedPlanType') || 'chapter';
      };
      selectedPlanTimeframe = function() {
        return settings('selectedPlanTimeframe') || '2:1';
      };
      load();
      vm.today = function() {
        vm.currentDate = moment();
        return updateTodaysReading();
      };
      vm.previousDay = function() {
        moveCurrentDate(-1);
        return updateTodaysReading();
      };
      vm.nextDay = function() {
        moveCurrentDate(1);
        return updateTodaysReading();
      };
      moveCurrentDate = function(direction) {
        return vm.currentDate = vm.currentDate.add(direction, 'd');
      };
    };
    return ng.module('readingPlan').controller('MainController', ['moment', 'ReadingPlanData', 'localStorageService', 'ScripturePassage', '$mdDialog', '$timeout', MainController]);
  })(angular, JSON);

}).call(this);



(function() {
  (function(ng, moment) {
    var ReadingPlanData;
    ReadingPlanData = [
      '$http', '$q', function($http, $q) {
        var dayPlan, readingDay;
        dayPlan = function(type, date) {
          var dayKey, error, success;
          dayKey = readingDay(date);
          if (dayKey) {
            success = function(response) {
              return $q.when(response.data);
            };
            error = function(response) {
              return $q.when({});
            };
            return $http({
              method: 'GET',
              url: "assets/data/" + type + "-bible-reading-plan/" + dayKey + ".json"
            }).then(success, error);
          } else {
            return $q.when({});
          }
        };
        readingDay = function(value) {
          var asMoment;
          asMoment = moment(value);
          switch (asMoment.day()) {
            case 0:
              return null;
            default:
              return asMoment.dayOfYear() - asMoment.week();
          }
        };
        return {
          dayPlan: dayPlan,
          readingDay: readingDay
        };
      }
    ];
    return ng.module('readingPlan').factory('ReadingPlanData', ReadingPlanData);
  })(angular, moment);

}).call(this);



