(function() {
  (function(ng) {
    var EsvBibleOrgReader;
    EsvBibleOrgReader = function($window) {
      var load;
      load = function(passage) {
        return $window.open("http://www.esvbible.org/" + ($window.encodeURIComponent(passage)) + "/", '_blank');
      };
      return {
        load: load,
        title: 'esvbible.org'
      };
    };
    return ng.module('readingPlan').factory('EsvBibleOrgReader', ['$window', EsvBibleOrgReader]).run([
      'ScripturePassage', 'EsvBibleOrgReader', function(ScripturePassage, EsvBibleOrgReader) {
        return ScripturePassage.register(EsvBibleOrgReader);
      }
    ]);
  })(angular);

}).call(this);



(function() {
  (function(ng) {
    var ScripturePassage;
    ScripturePassage = function() {
      var readers, registerReader;
      readers = [];
      registerReader = function(reader) {
        return readers.push(reader);
      };
      return {
        register: registerReader,
        readers: readers
      };
    };
    return ng.module('readingPlan').factory('ScripturePassage', [ScripturePassage]);
  })(angular);

}).call(this);



(function() {
  (function(ng) {
    var YouVersionBibleReader;
    YouVersionBibleReader = function($window, osisCodes) {
      var detailsToFormat, load, parseChapterVerse, startToEnd, toYouVersionFormat;
      toYouVersionFormat = function(passage) {
        var details;
        details = startToEnd(passage);
        if ('end' in details) {
          return (detailsToFormat(details, 'start')) + "+" + (detailsToFormat(details, 'end'));
        } else {
          return "" + (detailsToFormat(details, 'start'));
        }
      };
      detailsToFormat = function(details, key) {
        var chapterVerse;
        chapterVerse = details[key];
        if ('verse' in chapterVerse) {
          return details.book + "." + chapterVerse.chapter + "." + chapterVerse.verse;
        } else {
          return details.book + "." + chapterVerse.chapter;
        }
      };
      startToEnd = function(passage) {
        var details, parts, verseTokenz, verses;
        details = {};
        if (passage) {
          parts = passage.split(' ');
          details.book = osisCodes[parts[0]];
          if (parts.length > 1) {
            verses = parts[1];
            if (verses.indexOf('-') >= 0) {
              verseTokenz = verses.split('-');
              details.start = parseChapterVerse(verseTokenz[0]);
              if (verseTokenz.length > 1) {
                details.end = parseChapterVerse(verseTokenz[1], true);
                details.end.chapter = details.end.chapter || details.start.chapter;
              }
            } else {
              details.start = parseChapterVerse(verses);
            }
          }
        }
        return details;
      };
      parseChapterVerse = function(cv, isEnd) {
        var tokenz;
        if (cv.indexOf(':') >= 0) {
          tokenz = cv.split(':');
          return {
            chapter: tokenz[0],
            verse: tokenz[1]
          };
        } else {
          if (isEnd) {
            return {
              verse: cv
            };
          } else {
            return {
              chapter: cv
            };
          }
        }
      };
      load = function(passage) {
        var youVersionFormat;
        youVersionFormat = toYouVersionFormat(passage);
        console.log("Loading YouVersion Link: " + youVersionFormat);
        return $window.open("youversion://bible?reference=" + youVersionFormat, '_blank');
      };
      return {
        load: load,
        title: 'YouVersion Bible App'
      };
    };
    return ng.module('readingPlan').factory('YouVersionBibleReader', ['$window', 'osisCodes', YouVersionBibleReader]).run(['ScripturePassage', 'YouVersionBibleReader', function(ScripturePassage, YouVersionBibleReader) {}]);
  })(angular);

}).call(this);



